#root{
    max-width: 500px;
    margin: 0 auto;
}




.header{
    display: inline-block;
    width: 50%;
}
.search{
    border-radius: 5px;
    border: none;
    padding: 10px;
    width: 50%;
    font-family: 'Fira Sans', sans-serif;
}

.buttonsPanel{
    float: right;
    display: inline-block;
}

.filterButton{
    height: 35px;
    width: 50px;
    margin-right: 0;
    background: #fff;
    border: none;
    margin-left: 5px;
    border-radius: 5px;
    font-family: 'Fira Sans', sans-serif;
    cursor: pointer;
}

@media (max-width: 400px) {
    .buttonsPanel{
        float: none;
        margin-top: 15px;
    }
    .search{
        width: 93%;
    }
}

.todoListItem{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
}

.manageTaskPanel .taskPanelButton{
    padding: 7px;
    border: 1px solid #dddddd;
    border-radius: 5px;
    display: inline-block;
    width: 15px;
    text-align: center;
}

.taskPanelButton{
    position: relative;
    z-index: 10;
}

.manageTaskPanel .taskPanelButton:first-child{
    margin-right: 10px;
    color: #b30000;
    border-color: #b30000;
}

.manageTaskPanel .taskPanelButton:first-child:hover{
    background: rgba(179, 0, 0, 0.21);
}

.manageTaskPanel .taskPanelButton:last-child{
    color: #005a00;
    border-color: #005a00;
}

.manageTaskPanel .taskPanelButton:last-child:hover{
    background: rgba(0, 90, 0, 0.22);
}

.done{
    text-decoration: line-through;
}

.important{
    font-weight: bold;
}

.taskList{
    list-style: none;
    padding-left: 0;
}

.taskList li{
    cursor: pointer;
    border-radius: 5px;
    background: #fff;
}

.taskList li:hover, .filterButton:hover{
    background: aliceblue;
}

.taskList li:not(:last-child){
    border-bottom: none;
    margin-bottom: 5px;
}
.todoStatus{
    display: inline-block;
    text-align: right;
    width: 50%;
}

.addTodoInput{
    border-radius: 3px;
    border: none;
    padding: 10px;
    width: 75%;
}

.addTodoButton{
    margin-left: 15px;
    height: 35px;
    width: 80px;
    font-family: 'Fira Sans', sans-serif;
    cursor: pointer;
    border-radius: 3px;
    background: #fff;
    border: none;
}

.addTodoButton:hover{
    background: aliceblue;
}
